var Mock = require("mockjs");
import auth from "./auth";

const baseURLPRO = "/api/";

let apiArr = [
  "enterprise/im/getContacts",
  "enterprise/im/setting",
  "common/pub/bindUid",
  "common/pub/login",
  "common/pub/logout",
  "common/pub/bindUid",
  "common/pub/offline",
  "common/pub/bindGroup",
  "enterprise/group/groupuserlist",
  "enterprise/im/getMessageList",
  "enterprise/im/sendMessage",
  "enterprise/group/getAllUser",
  "enterprise/im/setChatTop"
];

apiArr.map(item => {
  Mock.mock(baseURLPRO + item, "post", auth[item]);
});

// 登录配置 相关
// Mock.mock(baseURLPRO+api.REGISTER_SET, 'post', auth[api.REGISTER_SET])

export default Mock;



