import Vue from 'vue'
import Vuex from 'vuex'
import Lockr from 'lockr'
import {
    addAuth,
    removeAuth
} from '@/utils/auth'
import {
    loginAPI,
    logoutAPI
} from '@/api/login'
import {
    resetRouter
} from '@/router'



Vue.use(Vuex)


const state = {
    userInfo: null, // 用户信息
    // 权限信息
    allAuth: null, // 总权限信息 默认空 调整动态路由
    socketAction: ''
}

const mutations = {
    SET_USERINFO: (state, userInfo) => {
        let userInfos = {
          user_id: 2,
          account: "13800000002",
          realname: "熊大",
          password: "2cb4ecb7fd5295685e275edc7d44e02e",
          salt: "srww",
          avatar: require("../assets/images/2.png"),
          email: "xiekunyu@kaishanlaw.com",
          remark: "123",
          name_py: "xiongda",
          setting: {
            theme: "default",
            hideMessageName: false,
            hideMessageTime: false,
            avatarCricle: true,
            sendKey: 1,
            is_voice: "false",
            isVoice: true,
            isShowGameTip: "false",
            isShowSet: "false"
          },
          create_time: "2019-04-15 23:24:25",
          update_time: "2019-05-16 23:16:26",
          login_count: 4,
          last_login_time: 1558020894,
          last_login_ip: "223.87.209.87",
          isdelete: 0,
          status: 1
        };
        state.userInfo = userInfo || userInfos;
        localStorage.setItem("loginUserInfo", JSON.stringify(userInfos));
        Lockr.set("UserInfo", { data: userInfos });
    },
    SET_AUTH: (state, data) => {
        let datas = data || {};
        const token = datas.authToken || {
          data: "083b2oUM6M46138gL36RE27muzcxhMzqm0G9jSHHjihzzdh0GyOhjks"
        };
        const sessionId = datas.sessionId || {
          data: "d76d8e056988960e8d9e825e11916fda"
        };
        Lockr.set('authToken', token)
        Lockr.set('sessionId', sessionId)
        addAuth(token, sessionId)
    },
    catchSocketAction(state, data) {
        state.socketAction = data;
    },
}

const actions = {
    // 登录
    Login({
        commit,
        dispatch
    }, userInfo) {
        return new Promise((resolve, reject) => {
            loginAPI(userInfo).then(res => {
                const data = res.data || {}
                commit('SET_AUTH', data)
                commit('SET_USERINFO', data.userInfo||{})
                resolve(res)
            }).catch(error => {
                console.log(error);
                dispatch('LogOut')
                reject(error)
            })
        })
    },

    // 获取用户信息
    GetUserInfo({
        commit,
        state
    }) {
        return new Promise((resolve, reject) => {
            // adminUsersReadAPI().then(response => {
            //     // 邮件信息 走之前的逻辑
            //     commit('SET_USERINFO', response.data)
            //     resolve(response)
            // }).catch(error => {
            //     reject(error)
            // })
        })
    },

    // 登出
    LogOut({
        commit
    }) {
        return new Promise((resolve, reject) => {
            logoutAPI().then(() => {
                /** flush 清空localStorage .rm('authToken') 按照key清除 */
                Lockr.rm('authToken');
                Lockr.rm('sessionId');
                removeAuth()
                resetRouter()
                resolve()
            }).catch(error => {
                reject(error)
            })
        })
    },
    // 获取权限
    // getAuth({
    //     commit
    // }) {
    //     return new Promise((resolve, reject) => {
    //         adminIndexAuthListAPI().then((response) => {
    //             const data = response.data
    //             Lockr.set('authList', data)
    //             data.wkFirstModel = data.firstModel
    //             commit('SET_ALLAUTH', data)
    //             resolve(data)
    //         }).catch(error => {
    //             reject(error)
    //         })
    //     })
    // },
}

export default new Vuex.Store({
    state,
    mutations,
    actions
})


